import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

export const inoutwardMasterFunction = (builder) => {
  return builder.query({
    query: (data) => `/v2/ward/institute/${data?.id}/query`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => response?.inoutward,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const inoutwardRecieveRequestPendingListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v2/ward/reveive/pending/${data?.sid}/query?page=${data?.page}&limit=${data?.limit}&search=${data?.search}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.outward,
        };
      else
        return {
          page: arg?.page,
          list: response?.outward,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
