  useInoutwardRecieveRequestPendingListQuery,
  useInoutwardRecieveRequestApprovedListQuery,
  useInoutwardSentRequestPendingListQuery,
  useInoutwardSentRequestApprovedListQuery,
  useInoutwardCreateInOutwardMutation,
  useInoutwardApprovedOutwardMutation,
  useInoutwardReadyToPublishListQuery,
  useInoutwardFeedPublishedListQuery,
  useInoutwardOutwardListQuery,
