export const useInoutwardMaster = (args) => {
  const { data, refetch, isFetching } = useInoutwardMasterQuery(args.data, {
    skip: args.skip,
  });
  return {
    inoutwardMaster: data,
    inoutwardMasterRefetch: refetch,
    inoutwardMasterLoading: isFetching,
  };
};

