import { useLocation } from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import {
  useInoutwardApprovedOutward,
  useInoutwardOutwardInwardDetail,
  useInoutwardRejectOutward,
} from "../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import styleFeedback from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import { Fragment, useState } from "react";
import FormFileView from "../../../Ui/Input/FormFileView";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import InOutwardSentRequestPendingCardMenu from "../InOutwardRequest/Sent/InOutwardSentRequestPendingCardMenu";

const InOutwardDetail = ({ staffId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [disabled, setDisabled] = useState(false);
  const [inoutwardApprovedOutward] = useInoutwardApprovedOutward();
  const [inoutwardRejectOutward] = useInoutwardRejectOutward();

  const [step, setStep] = useState("");

  const onClose = () => {
    setStep("");
  };

  const onMenu = () => {
    setStep(1);
  };

  const {
    inoutwardOutwardInwardDetail,
    inoutwardOutwardInwardDetailLoading,
    inoutwardOutwardInwardDetailRefetch,
  } = useInoutwardOutwardInwardDetail({
    data: {
      oid: getQuery.state?.outward?._id,
    },
    skip: !getQuery.state?.outward?._id,
  });
  const onApproved = (e) => {
    if (getQuery.state?.outward?._id && staffId) {
      setDisabled((pre) => !pre);
      inoutwardApprovedOutward({
        sid: staffId,
        oid: getQuery.state?.outward?._id,
      })
        .then(() => {
          inoutwardOutwardInwardDetailRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onReject = (e) => {
    if (getQuery.state?.outward?._id && staffId) {
      setDisabled((pre) => !pre);
      inoutwardRejectOutward({
        sid: staffId,
        oid: getQuery.state?.outward?._id,
      })
        .then(() => {
          inoutwardOutwardInwardDetailRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>
            {getQuery.state?.outward?.subject ??
              getQuery.state?.outward?.name ??
              ""}
            {" - "}{" "}
            <span
              style={{
                fontSize: "13px",
              }}
            >
              ({getQuery.state?.isLastShow})
            </span>
          </h6>
          <div className={style.fm_search}>
            {/* <CreateButton
              label="add"
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                width: "fit-content",
              }}
              onAction={() => setStep(1)}
            /> */}
            {inoutwardOutwardInwardDetail?.generated_report ? (
              <a
                href={`${pdfCdnUrl}/${inoutwardOutwardInwardDetail?.generated_report}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={`${assestsUserFinanceUrl}/receipt.svg`}
                  alt="receipt icon"
                  style={{
                    width: "1.5rem",
                    height: "1.5rem",
                  }}
                />
              </a>
            ) : null}
            {inoutwardOutwardInwardDetail?.outward_type === "Approved" ||
            !getQuery.state?.menuControll ? null : (
              <img
                src={`${assestsUserFinanceUrl}/edit.svg`}
                alt="edit icon"
                title="Menu"
                onClick={onMenu}
                style={{
                  height: "1.5rem",
                  width: "1.5rem",
                }}
              />
            )}
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {inoutwardOutwardInwardDetail?.model_type === "INWARD" ? (
          <div className={styleFeedback.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("name")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {inoutwardOutwardInwardDetail?.name ?? ""}
              </span>
            </h6>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("no")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {inoutwardOutwardInwardDetail?.number ?? ""}
              </span>
            </h6>
            <h6>
              {t("date")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {dayjs(inoutwardOutwardInwardDetail?.date)?.format(
                  "DD/MM/YYYY"
                ) ?? ""}
              </span>
            </h6>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("type")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {inoutwardOutwardInwardDetail?.outward_type ?? ""}
              </span>
            </h6>
            {getQuery.state?.isApproved === "YES" ? (
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("prepared_by")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {`${
                    inoutwardOutwardInwardDetail?.prepare_by?.staffFirstName ??
                    ""
                  } ${
                    inoutwardOutwardInwardDetail?.prepare_by?.staffMiddleName ??
                    ""
                  } ${
                    inoutwardOutwardInwardDetail?.prepare_by?.staffLastName ??
                    ""
                  }`}
                </span>
              </h6>
            ) : null}
            {inoutwardOutwardInwardDetail?.image?.length > 0 ? (
              <Fragment>
                <h6
                  style={{
                    marginBlock: "1rem 0.5rem",
                  }}
                >
                  {t("image")}
                </h6>
                {inoutwardOutwardInwardDetail?.image?.map((dt) => (
                  <FormFileView
                    key={dt}
                    label={`${t("image")} :`}
                    value={{
                      key: dt,
                    }}
                    customFormInputContainer={{
                      marginBlock: "0",
                    }}
                  />
                ))}
              </Fragment>
            ) : null}

            {inoutwardOutwardInwardDetail?.attachment?.length > 0 ? (
              <Fragment>
                <h6
                  style={{
                    marginBlock: "1rem 0.5rem",
                  }}
                >
                  {t("attachment")}
                </h6>
                {inoutwardOutwardInwardDetail?.attachment?.map((dt) => (
                  <FormFileView
                    key={dt}
                    label={`${t("attachment")} :`}
                    value={{
                      key: dt,
                    }}
                    customFormInputContainer={{
                      marginBlock: "0",
                    }}
                  />
                ))}
              </Fragment>
            ) : null}
            {getQuery.state?.isApproved === "YES" ? null : (
              <>
                <h6
                  style={{
                    marginBlock: "1rem 0.5rem",
                  }}
                >
                  {t("approval_for")}
                </h6>

                {inoutwardOutwardInwardDetail?.approvals_for?.map(
                  (ap, index) => (
                    <h6
                      key={`${index}${ap?.staff?._id}`}
                      style={{
                        fontWeight: "400",
                      }}
                    >
                      {`${ap?.staff?.staffFirstName ?? ""} ${
                        ap?.staff?.staffMiddleName ?? ""
                      } ${ap?.staff?.staffLastName ?? ""}`}
                      {" - "}
                      {ap?.status === "Pending" ? (
                        <span
                          style={{
                            color: "rgb(231, 75, 75)",
                          }}
                        >
                          ({`${ap?.status ?? ""}`})
                        </span>
                      ) : (
                        <span
                          style={{
                            color: "#00f",
                          }}
                        >
                          ({`${ap?.status ?? ""}`})
                        </span>
                      )}
                    </h6>
                  )
                )}
              </>
            )}
          </div>
        ) : (
          <div className={styleFeedback.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("outward_no")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {inoutwardOutwardInwardDetail?.outward_number ?? ""}
              </span>
            </h6>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("subject_only")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {inoutwardOutwardInwardDetail?.subject ?? ""}
              </span>
            </h6>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("type")}
              {": "}
              <span
                style={{
                  fontWeight: "400",
                }}
              >
                {inoutwardOutwardInwardDetail?.outward_type ?? ""}
              </span>
            </h6>

            <h6
              style={{
                whiteSpace: "break-spaces",
                fontWeight: "400",
              }}
            >
              <span
                style={{
                  fontWeight: "500",
                }}
              >
                {t("body")}
              </span>

              {": "}
              {inoutwardOutwardInwardDetail?.body ?? ""}
            </h6>

            {getQuery.state?.isApproved === "YES" ? (
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("prepared_by")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {`${
                    inoutwardOutwardInwardDetail?.prepare_by?.staffFirstName ??
                    ""
                  } ${
                    inoutwardOutwardInwardDetail?.prepare_by?.staffMiddleName ??
                    ""
                  } ${
                    inoutwardOutwardInwardDetail?.prepare_by?.staffLastName ??
                    ""
                  }`}
                </span>
              </h6>
            ) : null}
            {inoutwardOutwardInwardDetail?.image?.length > 0 ? (
              <Fragment>
                <h6
                  style={{
                    marginBlock: "1rem 0.5rem",
                  }}
                >
                  {t("image")}
                </h6>
                {inoutwardOutwardInwardDetail?.image?.map((dt) => (
                  <FormFileView
                    key={dt}
                    label={`${t("image")} :`}
                    value={{
                      key: dt,
                    }}
                    customFormInputContainer={{
                      marginBlock: "0",
                    }}
                  />
                ))}
              </Fragment>
            ) : null}

            {inoutwardOutwardInwardDetail?.attachment?.length > 0 ? (
              <Fragment>
                <h6
                  style={{
                    marginBlock: "1rem 0.5rem",
                  }}
                >
                  {t("attachment")}
                </h6>
                {inoutwardOutwardInwardDetail?.attachment?.map((dt) => (
                  <FormFileView
                    key={dt}
                    label={`${t("attachment")} :`}
                    value={{
                      key: dt,
                    }}
                    customFormInputContainer={{
                      marginBlock: "0",
                    }}
                  />
                ))}
              </Fragment>
            ) : null}

            {getQuery.state?.isApproved === "YES" ? null : (
              <>
                <h6
                  style={{
                    marginBlock: "1rem 0.5rem",
                  }}
                >
                  {t("approval_for")}
                </h6>

                {inoutwardOutwardInwardDetail?.approvals_for?.map(
                  (ap, index) => (
                    <h6
                      key={`${index}${ap?.staff?._id}`}
                      style={{
                        fontWeight: "400",
                      }}
                    >
                      {`${ap?.staff?.staffFirstName ?? ""} ${
                        ap?.staff?.staffMiddleName ?? ""
                      } ${ap?.staff?.staffLastName ?? ""}`}
                      {" - "}
                      {ap?.status === "Pending" ? (
                        <span
                          style={{
                            color: "rgb(231, 75, 75)",
                          }}
                        >
                          ({`${ap?.status ?? ""}`})
                        </span>
                      ) : (
                        <span
                          style={{
                            color: "#00f",
                          }}
                        >
                          ({`${ap?.status ?? ""}`})
                        </span>
                      )}
                    </h6>
                  )
                )}
              </>
            )}
          </div>
        )}
        {getQuery.state?.isApproved === "YES" ? (
          <>
            {inoutwardOutwardInwardDetail?.approvals_for?.map((ft, index) =>
              `${ft?.staff?._id}` === staffId ? (
                <>
                  {ft?.status === "Approved" ? (
                    <h6
                      key={`${index}${ft?.staff?._id}`}
                      style={{
                        fontWeight: "400",
                      }}
                    >
                      <span
                        style={{
                          color: "#00f",
                        }}
                      >
                        {`${ft?.status ?? ""}`}
                      </span>
                    </h6>
                  ) : (
                    <div className={style.fm_header_container}>
                      <CreateButton
                        label="approve"
                        customStyle={{
                          padding: "0.3rem 1rem",
                          width: "49%",
                        }}
                        onAction={onApproved}
                      />
                      {/* {inoutwardOutwardInwardDetail?.cancle_by_staff?.includes(
    staffId
  ) ? null : (
    <CreateButton
      label="reject"
      customStyle={{
        padding: "0.3rem 1rem",
        width: "49%",
        "--bg-button": "rgb(231, 75, 75, 0.15)",
        "--color-button": "rgb(231, 75, 75)",
      }}
      onAction={onReject}
    />
  )} */}
                    </div>
                  )}
                </>
              ) : null
            )}
          </>
        ) : null}
      </UserMemberContentWrapper>

      {inoutwardOutwardInwardDetailLoading && <QvipleLoader />}
      {disabled && <QvipleLoader />}
      {step === 1 ? (
        <InOutwardSentRequestPendingCardMenu
          onClose={onClose}
          onRefetch={inoutwardOutwardInwardDetailRefetch}
          menuData={inoutwardOutwardInwardDetail}
        />
      ) : null}
    </>
  );
};

export default InOutwardDetail;
